  display: flex;
}

.card:hover {
  border: 1px solid #f3f3f3;
}

.card__image {
  border: none;
  width: 160px;
  height: 134px;
  margin: 32px 0 0;
  padding: 0;
  display: block;
}

.card__profuctName {
  color: #060b35;
  width: 166px;
  height: 36px;
  margin: 40px 0 0;
  padding: 0;
  font-family: Roboto-medium, sans-serif;
