  font-weight: 400;
  line-height: 14px;
}

.card__stars {
  justify-content: space-between;
  width: 166px;
  margin-top: 16px;
  line-height: 16px;
  display: flex;
}

.card__stars body {
  margin: 0;
  padding: 0;
}

.card__stars .stars {
  margin: 0;
  padding: 0;
  display: flex;
}

.card__stars .stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin: 0;
  padding: 0;
}

.card__stars .stars__star:not(:last-child) {
  margin-right: 4px;
}

.card__stars .stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
